.dropdown {  
    display: inline-block;  
    position: relative;
    border-radius: 3px;
}  
.dropdown-content {  
    position:absolute;  
    background-color: lightgrey;  
    border-radius:4px;
    min-width: 200px;  
    display: none;  
    z-index: 1; 
}  
.dropdown-content button {  
    color: white; 
    background-color: #43a047;;
    border-radius: 4px;
    text-decoration: none;  
    display: block;  
    border:none;
}  
.dropdown-content button:hover {  
    background-color: lightgreen;  
}  
.dropdown:hover .dropdown-content {  
    display: block;  
}
